import { NgModule } from '@angular/core'; 
import { SharedModule } from 'app/shared/shared.module';
import { ManageStateCrudComponent } from './manage-state-crud/manage-state-crud.component';
import { CreateComponent } from './create/create.component';
import { ListComponent } from './list/list.component';
import { ManageStateRoutingModule } from './manage-state-routing.module';
import { ActionDialogComponent } from './action-dialog/action-dialog.component';
import { FuseSharedModule  } from '@fuse/shared.module';
import { ViewDialogComponent } from './view-dialog/view-dialog.component';
const COMPONENTS =[ManageStateCrudComponent, CreateComponent, ListComponent];
const COMPONENTS_DYNAMIC =[ActionDialogComponent,ViewDialogComponent];

@NgModule({
    imports:[SharedModule, ManageStateRoutingModule,FuseSharedModule],
    declarations : [COMPONENTS,COMPONENTS_DYNAMIC],
    entryComponents : [COMPONENTS_DYNAMIC]
})

export class ManageStateModule{}