import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManageStateCrudComponent } from './manage-state-crud/manage-state-crud.component';
import { TABS_DEMO_ROUTES } from './manage-state-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : '',component : ManageStateCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ManageStateRoutingModule{}